import React, { Component } from 'react';
import { BrowserRouter, Route, Link, Switch} from 'react-router-dom';
import BodyContentBlog from './BodyContentBlog';
import BlogItem from './BlogItem';
import logo from './logo.svg';
import './App.css';
import './bootstrap.css';

import lists from './data/lists.json';
const TileList = lists["TileList"];


export default class BlogBody extends Component {

  constructor(props){
    super(props);
    this.state = {blogs:[]};
  }


    componentWillMount(){
        this.props.db.database().ref('ContentBlog').on('value', (contentBlog) =>{
        let blogs = contentBlog.val();
        this.setState({
            blogs: blogs["blogs"]
          })
        });
     }

  render() {
    return (
      <Switch>
        <Route exact path='/blog'  render= {(props) => <BodyContentBlog blogs={this.state.blogs} {...props}/> } />
        <Route path='/blog/:blogId'  render= {(props) => <BlogItem db={this.props.db} id={this.state.blogId} {...props}/> } />
      </Switch>

    );
  }
}



