import React, { Component, form, input, textarea, button } from 'react';
import { BrowserRouter, Route, Link} from 'react-router-dom';
import './App.css';
import './bootstrap.css';
import Footer from './Footer';



export default class BlogPublish extends Component {

  constructor(props){
    super(props);
      this.state = {blogname:"",
      blogcontent:""};
     this.saveBlog = this.saveBlog.bind(this);
     this.handleNameChange = this.handleNameChange.bind(this);
     this.handleContentChange = this.handleContentChange.bind(this);
     this.handleImageChange = this.handleImageChange.bind(this);
     this.handleVideoChange = this.handleVideoChange.bind(this);

  }


  saveBlog(e){
        e.preventDefault();
        let imgFile = this.fileInput.files[0] != undefined ? this.fileInput.files[0] :"";
        const Blog =  {
          title: this.state.blogname ,
          content:  this.state.blogcontent === ""? imgFile : this.state.blogcontent,
          images:   this.state.blogImage === undefined? "":this.state.blogImage,
          shortDesc: this.state.blogname === undefined? "":this.state.blogname,
          video: this.state.blogVideo === undefined? "": this.state.blogVideo,
          publishedAt: Date.now()
        };

        const blogRef = this.props.db.database().ref('ContentBlog').child('blogs');
        const isit = blogRef.push(Blog).then((res)=> {
            console.log('published ');
        });

     }
     handleNameChange(e){
         this.setState({"blogname": e.target.value});
     }
     handleContentChange(e){
         this.setState({"blogcontent": e.target.value});
     }
     handleImageChange(e){
         this.setState({"blogImage": e.target.value});
     }
     handleVideoChange(e){
         this.setState({"blogVideo": e.target.value});
     }

  render() {
    return (


      <div>
      <div className="container" id="main">


          <div className="row">
                  <div className="page-header">
                  <h4 > Text Editor</h4>
                  </div>
                  <form onSubmit={this.saveBlog} className="form-horizontal" name="blogform" id="blogform">
                     <div className="form-group">
                       <input id="blogname"   className="col-sm-10"  type="text" placeholder="Blog name" onChange={this.handleNameChange}/>
                       </div>
                       <div className="form-group">
                          <textarea id="blogcontent"  className="col-sm-10"   placeholder="Start Typinggg" rows="3" onChange={this.handleContentChange} />
                        </div>
                        <div className="form-group">
                             <input type="file" id="imgFile" ref={input => {
                               this.fileInput = input;
                             }}/>
                          </div>
                          <div className="form-group">
                               <input id="blogImage"   className="col-sm-10"  type="text" placeholder="Image URL" onChange={this.handleImageChange}/>
                            </div>
                            <div className="form-group">
                                  <input id="blogVideo"   className="col-sm-10"  type="text" placeholder="Video URL" onChange={this.handleVideoChange}/>
                              </div>
                           <div className="form-group">   <button type="submit" className="btn btn-default">Submit</button></div>

                  </form>

                    </div>

                  <Footer/>
      </div>

      </div>



    );
  }
}



