import React, {Component} from 'react';

import Alink from './Alink';
import ListItems from './ListItems';
import { BrowserRouter, Route, Link} from 'react-router-dom';


export default class BodyHeaderCtrl extends Component{

constructor(props){
  super(props);
  this.state = {showNav: props.showNav, blogItems:0};
  this.showToggle = this.showToggle.bind(this);
}

showToggle(e){
  this.setState({showNav: !this.state.showNav});
}

componentWillMount(){
    this.props.db.database().ref('ContentBlog').child('blogs').on('value', (contentBlog) =>{

    this.setState({
        blogItems: contentBlog.numChildren()
      })
    });
 }


render(){

  return (
    <nav className="navbar navbar-default navbar-fixed-top">
      <div className ="container">
        <div className="navbar-header">

          <button type="button" className="navbar-toggle collapsed" data-toggle="collapse"
            data-target="#navbar" aria-expanded="false" aria-controls="navbar"
            onClick={this.showToggle}>
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <Link to="/">
          <Alink href="#" displayText="Kalyan Sundar" className="navbar-brand"></Alink>
          </Link>
       </div>
       <ListItems projectlist={this.props.projectlist} showNav={this.state.showNav} blogItems = {this.state.blogItems}></ListItems>
     </div>
   </nav>

  );
}

}



