import React, {Component} from 'react';
import ContentRowBlockList from './ContentRowBlockList';

export default class ContentRowBlock extends Component{


  constructor(props){
    super(props);
    this.state= {collapseStyle: "panel-footer collapse", buttonOC: "btn btn-default collapse.in" };
    this.showElement = this.showElement.bind(this);
    this.hideElement = this.hideElement.bind(this);
  }



  showElement(e){
      this.setState( {collapseStyle: "panel-footer collapse.in" , buttonOC: "btn btn-default collapse"});
  }

  hideElement(e){
    this.setState({collapseStyle: "panel-footer collapse" , buttonOC: "btn btn-default collapse.in"});
    }


render(){
  return(
    <div className="col-md-4 col-sm-6 ">
          <div className="panel panel-default thumbnail">
            <div className="panel-heading">
            <a href="#" className="pull-right" />
            <h4>
            {this.props.item.title}
            </h4>
            </div>
            <div className="panel-body">
              <p className="contentalign"> {this.props.item.description}</p>
                 <button type="button" class={this.state.buttonOC} onClick ={this.showElement} >+</button>

             </div>

             <div class=  {this.state.collapseStyle}>
                <button type="button" class="btn btn-default" onClick={this.hideElement}>x</button>
               <ContentRowBlockList item = {this.props.item} />

             </div>

            </div>

        </div>
      );

}

}



