import React, {Component} from 'react';

export default class Jumbotron extends Component{

  constructor(props){
    super(props);

  }


render(){
  return(
  <div className="jumbotron">
        <h3>Little about me!</h3>
        <p className="lead">Technology Generalist with an MBA :/ </p>
        <p>
          <a className="btn btn-social-icon btn-twitter" href="https://twitter.com/kalysundar" role="button">
            <i className="fa fa.pull-left fa-twitter" />
          </a>
          <a className="btn btn-social-icon btn-linkedin" href="https://www.linkedin.com/in/kalyansundar" role="button">
            <i className="fa  fa.pull-left fa-linkedin" />
          </a>
          <a className="btn btn-social-icon btn-github " href="https://github.com/kayestee" role="button">
            <i className="fa fa.pull-left fa-github" />
          </a>
          <a className="btn btn-social-icon btn-medium " href="https://medium.com/@kalysundar" role="button">
            <i className="fa  fa.pull-left fa-medium" />
          </a>
        </p>
      </div>

  );
}

}



