import React, {Component} from 'react';
import {  Link} from 'react-router-dom';

export default class BlogRowBlock extends Component{


  constructor(props){
    super(props);

  }


render(){

  return(
    <div className="row" >
    <div className="col-md-10 ">

          <div className="panel panel-default thumbnail">
            <div className="panel-heading">

              <h6 className="text-left ">
              {this.props.item.publishedAt === undefined ? "" :  <span className="label label-info ">  {new Date(this.props.item.publishedAt).toDateString()} </span>}
                </h6>
                   <Link to={{ pathname: `/blog/${this.props.id}`, query: { blogId: `${this.props.id}` }}}>
                    <h4>
                          {this.props.item.title}
                    </h4>
                    </Link>

            </div>
            <div className="panel-body">
                <p className="contentalign"> {this.props.item.shortDesc}</p>
              </div>

            </div>

        </div>
            </div>
      );

}

}



