import React, {Component} from 'react';
import { BrowserRouter, Route, Link} from 'react-router-dom';

import Alink from './Alink';
import ListItems from './ListItems';
import Footer from './Footer';
import BlogRow from './BlogRow';
import './bootstrap.css';
import Image from 'react-image-resizer';
import ReactPlayer from 'react-player';


export default class BlogItem extends Component{

constructor(props){
  super(props);
  this.state = {blog:""};
  
}

componentWillMount(){
    this.props.db.database().ref('ContentBlog').child('blogs').on('value', (individualBlog) =>{
    let blogs = individualBlog.val();
    this.setState({
        blog: blogs[this.props.match.params.blogId]
      })
    });
 }



render(){

  return (
    <div>
    <div className="container" id="main">
                <div className="row" >
                <div className="col-md-10 ">

                      <div className="panel panel-primary ">
                        <div className="panel-heading">
                                <h4>
                                      {this.state.blog.title}
                                    </h4>
                        </div>
                        <div className="panel-body">
                            <p className="contentalign"> {this.state.blog.shortDesc}</p>
                              <p className="contentalign"> {this.state.blog.content}</p>

                        </div>
                        {this.state.blog.images === undefined ? "" : <Image src={this.state.blog.images.testImg} height={140} width={140} className="img-circle"/>}
                        {this.state.blog.video === undefined ? "" : <ReactPlayer  url={this.state.blog.video.testVideo} playing/>}



                      </div>
                        </div>
                        </div>

                <Footer/>
    </div>

    </div>
  );
}

}



