import React, { Component } from 'react';
import { BrowserRouter, Route,  Link} from 'react-router-dom';

import logo from './logo.svg';
import './App.css';
import './bootstrap.css';
import BodyHeaderCtrl from './BodyHeaderCtrl.js';
import firebase  from './firebase';

import AppBody from './AppBody';
import BlogBody from './BlogBody';
import LoginPublish from './LoginPublish';

import lists from './data/lists.json';
const ProjectList = lists["ProjectList"];




class App extends Component {

  constructor(props){
    super(props);
    this.state = {db:firebase,
    showNav:false};

  }




  render() {
    return (

        <BrowserRouter >
        <div>
        <BodyHeaderCtrl projectlist={ProjectList} props={this.props} db={this.state.db} ></BodyHeaderCtrl>

        <Route exact path="/" component={AppBody} />
        <Route path="/blog"  render= {(props) => <BlogBody db={this.state.db} {...props}/> } />
          <Route path="/publish"  render= {(props) => <LoginPublish db={this.state.db} {...props}/> } />
        </div>
        </BrowserRouter>

    );
  }
}

export default App;



