import React, { Component, form, input, textarea, button } from 'react';
import { BrowserRouter, Route, Link} from 'react-router-dom';
import './App.css';
import './bootstrap.css';
import Footer from './Footer';
import BlogPublish from './BlogPublish';


const loggedin = false;

export default class LoginPublish extends Component {

  constructor(props){
    super(props);
      this.state = {Email:"",
      Password:"",
     loggedIn: false,
    user : null};
     this.loginBlog = this.loginBlog.bind(this);
     this.handleEmail = this.handleEmail.bind(this);
     this.handlePassword = this.handlePassword .bind(this);

  }

  componentWillMount(){

    this.authSubscription = this.props.db.auth().onAuthStateChanged((user) => {
      this.setState({
          loggedIn: false,
          user
      });
    });

  }


  loginBlog(e){
      e.preventDefault();

       this.props.db.auth().signInWithEmailAndPassword(this.state.Email, this.state.Password).catch(function(error) {
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            // ...
        });
        this.setState({loggedIn : true});
        this.forceUpdate();

     }
     handleEmail(e){
         this.setState({"Email": e.target.value});
     }
     handlePassword(e){
         this.setState({"Password": e.target.value});
     }

  render() {
    if(this.state.user) {
      return (
            <BlogPublish  db={this.props.db} props={this.props} />
        )
    }
    return (

      <div className="container" id="main">
      <div className="row">

        <div className="page-header">
        <h4 > Login</h4>
        </div>

          <form onSubmit={this.loginBlog} name="loginblog" id="loginblog">
           <div class="form-group">
             <label >Email address</label>
             <input type="email" class="form-control" id="exampleInputEmail1" placeholder="Email" onChange={this.handleEmail}/>
           </div>
           <div class="form-group">
             <label >Password</label>
             <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" onChange={this.handlePassword}/>
           </div>
           <button type="submit" class="btn btn-default">Submit</button>
          </form>

          <Footer/>
      </div>
        </div>


  );
  }
}



