import React,{Component} from 'react';
import SingleList from './SingleList';
import { BrowserRouter, Route, Link} from 'react-router-dom';

import './bootstrap.css';

export default class ListItems extends Component {
  constructor(props){
    super(props);
    this.state= {showDropDown: "dropdown",burgerCollapse: "collapse" };
    this.showElement = this.showElement.bind(this);
    this.hideElement = this.hideElement.bind(this);
  }


  renderList(){
    return  this.props.projectlist.map((item) => (
      <SingleList key={item.id} item={item}/>
    ));
  }

  showElement(e){
    this.setState( {showDropDown: "dropdown open"});
  }

  hideElement(e){
    this.setState({showDropDown: "dropdown"});
    }


  render(){
    return(
      <div id="navbar" className= {`navbar-collapse  ${this.props.showNav === true ? "collapse in" :"collapse"}`}>
      <ul className="nav navbar-nav">

        <li className= {this.state.showDropDown}  onMouseOver={this.showElement} onMouseOut={this.hideElement}  >
          <a href="#" className="dropdown-toggle" data-toggle="dropdown" role="button" >Projects <span className="caret"></span></a>
          <ul className="dropdown-menu" role="menu">
            {this.renderList()}
          </ul>
        </li>
      </ul>
            <Link to="/blog">
            <ul className="nav navbar-nav navbar-right nav-pills">
              <li className="active presentation">
                    <a href="#">Blog <span className="badge sub">{this.props.blogItems}</span></a> </li>

            </ul>

          </Link>
   </div>



    );
  }



}



