import React, {Component} from 'react';
import ContentRowBlock from './ContentRowBlock';

export default class ContentRow extends Component{

  constructor(props){
    super(props);
  }
  renderList(){
    
    return  this.props.TileList.map((item) => (
      <ContentRowBlock key={item.id} item={item}/>
    ));
  }

render(){
  return(
  <div className="row">

     {this.renderList()}
   </div>


  );
}

}



