import React, {Component} from 'react';
import SingleList from './SingleList';
import Alink from './Alink';
import './bootstrap.css';
export default class ContentRowBlockList extends Component{

  constructor(props){
    super(props);
  }

renderList(){
  return this.props.item.list.map((item) => (
      <Alink   key={item.id} href={item.link} className= "list-group-item" displayText={item.description} ></Alink>
    ));
}

render(){

    if( this.props.item.list  === "") {
      return(
          <p className="contentalign"> {this.props.item.otherside}</p>
      );
    } else {
      return (
        <div className="list-group">
                  {this.renderList()}
        </div>
   );
 }
}
}



