import React, {Component} from 'react';
import BlogRowBlock from './BlogRowBlock';

export default class BlogRow extends Component{

  constructor(props){
    super(props);

  }
  renderList(){
    return  Object.keys(this.props.blogs).map((key) => (
      <BlogRowBlock key={key} id={key} item={this.props.blogs[key]}/>
    ));
  }

render(){
  return(
       this.renderList()
);
}

}



