import React,{Component} from 'react';

import Alink from './Alink';


export default class SingleList extends Component {

  constructor(props){
    super(props);
  }


  render(){
    return(
        <li> <Alink href={this.props.item.link} displayText={this.props.item.description} ></Alink> </li>
    );
  }



}



